  //Get Prescriptions list and payment pending restriction
  async prescriptionAndPayRestriction() { 
   
    let tenantID:any=[];
    this.authService
    .getPtCircleList(this.userdetail.userID)
    .subscribe(async data=>{
      console.log("Circle data", data);
      this.circleDetail = data;
      if (this.circleDetail!=null) {
        for(let cd of this.circleDetail) {
          tenantID.push(cd.vhEntity.circleCode);
        }
        let search={
          tenantid: tenantID
        }
        console.log("search",JSON.stringify(search));
        let loading = await this.loadingCtrl.create({
          spinner: 'bubbles',
          message: ''
        });
        await loading.present(); 
        this.authService
        .singleCirclePrescriptionList(this.userID, search)
        .subscribe(data=>{
          console.log("prescription list", data);
          this.allItems = data;
          loading.dismiss();
          this.setPage(1);
        });

        // payment restriction
        let billObj:any = {
          circleCodeList: tenantID
        }
        console.log("billobj========>", billObj, tenantID);  
        this.authService
        .getConsultFeeDetailAllCircle(billObj, this.userID)
        .subscribe(data=>{
          console.log("data", data);        
          if (data!=null) {
            this.feeDetail=data;
            this.pendingBillDetail = this.feeDetail.filter(data=>
              data.paymentStatus=="Pending"
            );

            console.log("fee status======>", this.pendingBillDetail);
            this.paymentStatus=this.feeDetail.paymentStatus;
            if (this.pendingBillDetail.length>=1) {
             console.log("pending payment");
             this.pendingPaymentStatus=true;
             this.consultPayment();
            }
          }
        });
      }
    });
  }

