  async downloadDocument(keyname:any) {
    let loading = await this.loadingCtrl.create({
      spinner: 'bubbles',
      message: ''
    });
    await loading.present();
    this.authService
    .downloadDocumentByKeyname(keyname) 
    .subscribe(data=>{
      // debugger;
      console.log(data);
      this.documentObject=data;
      loading.dismiss();
      if(this.documentObject!=null) {
        if(this.platform.is('android') || this.platform.is('mobileweb')) {   
          this.viewMobilePdf();
        } else {
          this.viewPdf();
        }
      }
    });
  }

  viewPdf() {
    /* let pdfWindow = window.open('', '_self'); */
    let pdfWindow = window.open();
    var pdf = pdfWindow.document.write("<iframe width='100%' height='100%' src='data:application/pdf;base64, " + this.documentObject + "'></iframe>");
  }

  viewMobilePdf() {
    let filename="prescription.pdf";
    const writeDirectory = this.platform.is('ios') ? this.file.dataDirectory : this.file.externalDataDirectory;
    var blob= new Blob;
    blob=this.convertBase64ToBlob(this.documentObject, 'data:application/pdf;base64');
    this.file.writeFile(writeDirectory, filename, blob, {replace: true})
    .then(() => {
      this.opener.open(writeDirectory + filename, 'application/pdf')
      .catch(() => {
        console.log('Error opening pdf file');
      });
    })
    .catch(() => {
      console.error('Error writing pdf file');
    });
  }

  convertBase64ToBlob(b64Data, contentType): Blob {
    contentType = '';
    const sliceSize = 512;
    b64Data = b64Data.replace(/^[^,]+,/, '');
    b64Data = b64Data.replace(/\s/g, '');
    const byteCharacters = window.atob(b64Data);
    const byteArrays = [];
    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      const slice = byteCharacters.slice(offset, offset + sliceSize);
      const byteNumbers = new Array(slice.length);
      for (let i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }
      const byteArray = new Uint8Array(byteNumbers);
      byteArrays.push(byteArray);
    }
    return new Blob(byteArrays, {type: contentType});
  }

